<template>
	<view>
		<u-mask :show="show" :mask-click-able="false">
			<view v-if="show" class="loading">
				<Loading text="加载中.." :mask="true" click="true" ref="loading"></Loading>
			</view>
		</u-mask>
		<u-navbar :border-bottom="false" :is-back="false">
			<view class="slot-wrap">
				<u-avatar size="70" :src="userHead"></u-avatar>
				<view v-if="currentRole.classRole=='老师'" class="name">
					{{currentRole.username}}老师
				</view>
				<view v-else-if="currentRole.classRole=='学生'" class="name">
					{{currentRole.username}}同学
				</view>
				<u-icon @click="backToIndex" color="#1296db" custom-prefix="custom-icon" name="qiehuanyonghu"
					:size="40"></u-icon>
			</view>
		</u-navbar>
		<view class="wrap">
			<view class="u-tabs-box">
				<u-tabs-swiper :bar-style="barStyle" inactive-color="#9f9f9f" activeColor="#333333" ref="tabs"
					:list="list" :current="current" @change="change" :is-scroll="false" swiperWidth="750" font-size="36"
					bar-width="80">
				</u-tabs-swiper>
				<u-badge :is-dot="true" type="error" :offset="[16,100]"></u-badge>
			</view>
			<swiper class="swiper-box" :current="swiperCurrent" @transition="transition"
				@animationfinish="animationfinish">
				<swiper-item class="swiper-item">
					<scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom">
						<view style="content: ''; overflow: hidden;"></view>
						<view class="select">
							<view class="sub">
								<u-subsection button-color="#e1e1e1" bg-color="#fff" :list="chooseList" :current="0">
								</u-subsection>
							</view>
							<u-icon name="choose" custom-prefix="custom-icon" size="50"></u-icon>
						</view>
						<view class="page-box">
							<notice-card :noticeList="res" v-for="(res, index) in orderList[0]" :key="res.id">
							</notice-card>
							<u-loadmore :status="loadStatus[0]" bgColor="#f7f8fa"></u-loadmore>
						</view>
					</scroll-view>
				</swiper-item>
				<swiper-item class="swiper-item">
					<scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom">
						<view style="content: ''; overflow: hidden;"></view>
						<view class="class-operation">
							<view hover-class="hover" class="card" @click="goCreateClass">
								<view class="operation">
									<view class="name">
										创建班级
									</view>
									<view class="desc">
										管理员可转让
									</view>
								</view>
								<u-avatar mode="square" src="/static/img/establish.png"></u-avatar>
								<!-- <u-image height="60rpx" src="/static/img/establish.png"></u-image> -->
							</view>
							<u-line :hair-line="false" color="#d1d1d1" direction="col" length="80rpx"></u-line>
							<view hover-class="hover" class="card" @click="goJoinClass">
								<view class="operation">
									<view class="name">
										加入班级
									</view>
									<view class="desc">
										已存在的班级
									</view>
								</view>
								<u-avatar mode="square" src="/static/img/join.png"></u-avatar>

							</view>
						</view>
						<view class="page-box">
							<classCard :classList="res" v-for="(res, index) in orderList[1]" :key="res.id" />
							<u-loadmore :status="loadStatus[1]" bgColor="#f7f8fa"></u-loadmore>
						</view>
					</scroll-view>
				</swiper-item>
			</swiper>
		</view>
		<u-tabbar :list="tabbars" :mid-button="true" active-color="#00aaff"></u-tabbar>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations
	} from 'vuex'
	import classCard from "@/components/classCard/classCard.vue";
	import noticeCard from "@/components/noticeCard/noticeCard.vue"
	import Loading from "@/components/loading.vue";
	export default {
		components: {
			// selectSwitch
			classCard,
			Loading,
			noticeCard
		},
		data() {
			return {
				tabbars: '',
				userName: "",
				userHead: "",
				show: true,
				barStyle: {
					background: "#00aaff",
				},
				orderList: [
					[],
					[],
				],
				chooseList: [{
						name: '全部'
					},
					{
						name: '我的'
					},
				],
				isWidth: 0, //每个导航栏占位
				dataList: [],
				list: [{
						name: '班级广播'
					},
					{
						name: '我的班级',
						count: 0,
					}
				],
				current: 0,
				classID: [],
				swiperCurrent: 0,
				currentNoticePage: 0,
				currentClassPage: 0,
				tabsHeight: 0,
				dx: 0,
				onePageData: 3,
				// scrollTop: 0,
				count: 0,
				loadStatus: ['loadmore', 'nomore'],
				hasJoined: 0,
				classIdList: [],
				noticeList: []
			};
		},
		onLoad() {
			this.hasJoined = this.userInfo.joined_class.length
			this.tabbars = this.tabbar
			this.userHead = uni.getStorageSync('userInfo').avatar
			console.log("id为" + this.uid)
			if (this.currentRole.classRole == '老师') {
				this.getTeacherClassList()
			} else {
				this.getStudentClassList()
			}
		},
		mounted() {},
		computed: {
			...mapState(['uid', 'currentRoleClassList', 'currentRole', 'hasLogin', 'userInfo', 'hasAuth', 'tabbar'])
		},
		methods: {
			...mapMutations(['setClassList']),
			backToIndex() {
				uni.reLaunch({
					url: '../../../pages/index/index'
				});
			},
			async getStuCount(classID) {
				const db = uniCloud.database()
				var count = Number
				await db.collection('class-list').where({
					'_id': classID,
					'class_members.class_role': '学生'
				}).get({
					getCount: true
				}).then((res => {
					console.log("!!!!!", res)
					count = res.result.count

				}))
				return count
			},
			async getTeacherClassList() {
				const db = uniCloud.database()
				const dbCmd = db.command
				await db.collection('class-list').where({
						class_members: dbCmd.all([
							dbCmd.elemMatch({
								members_id: this.uid,
								class_role: this.currentRole.classRole,
							})
						]),
					})
					.get().then((res) => {
						console.log("dataList:", res)
						this.dataList = res.result.data
						this.setClassList(this.dataList)
					}).catch((err) => {
						console.log(err)
					})
				for (let item of this.dataList) {
					item.id = item._id
					item.className = item.class_name
					item.classSize = item.class_size
					item.code = item._id
					item.headImg = this.userHead
					item.joined = await this.getStuCount(item._id)
					item.noJoin = item.class_size - item.joined
					this.classIdList.push(item._id)
				}
				console.log("????", this.dataList)
				// this.getOrderList(1)
				this.getNoticeList()
			},
			async getStudentClassList() {
				const db = uniCloud.database()
				await db.collection('class-list').where({
						'class_members.members_id': this.uid,
						'class_members.class_role': this.currentRole.classRole,
						'class_members.name': this.currentRole.username
					})
					.get().then((res) => {
						uni.hideLoading();
						console.log(res)
						this.dataList = res.result.data
						this.setClassList(this.dataList)
					}).catch((err) => {
						console.log(err)
					})
				for (let item of this.dataList) {
					item.className = item.class_name
					item.classSize = item.class_size
					item.code = item._id
					item.headImg = this.userHead
					item.joined = await this.getStuCount(item._id)
					item.noJoin = item.class_size - item.joined
					this.classIdList.push(item._id)
				}
				console.log("????", this.dataList)
				this.getNoticeList()
				this.show = false
				// this.getStuCount()
				// this.getStuCount()
			},
			async getNoticeList() {
				console.log("classIdList:", this.classIdList)
				const db = uniCloud.database()
				const dbCmd = db.command
				await db.collection('class-notice,class-list,uni-id-users')
					.where({
						'class_id._id': dbCmd.in(this.classIdList)
					})
					.field(
						'class_id{class_name,class_size,_id},title,content,type,form_item,release_time,publisher_id{_id,nickname},confirmed'
					)
					.orderBy('release_time desc')
					.get({
						getCount: true
					}).then((res) => {
						console.log("noticeList：", res.result.data)
						for (let i = 0; i < res.result.data.length; i++) {
							for (let j = 0; j < res.result.data[i].class_id.length; j++) {
								if (this.classIdList.indexOf(res.result.data[i].class_id[j]._id) != -1) {
									this.noticeList.push({
										_id: res.result.data[i]._id,
										type: res.result.data[i].type,
										title: res.result.data[i].title,
										content: res.result.data[i].content,
										publisher: res.result.data[i].publisher_id[0].nickname,
										publisherId: res.result.data[i].publisher_id[0]._id,
										release_time: res.result.data[i].release_time,
										confirmed: res.result.data[i].confirmed,
										class_id: res.result.data[i].class_id[j]._id,
										class_name: res.result.data[i].class_id[j].class_name,
										class_size: res.result.data[i].class_id[j].class_size,
										form_item:res.result.data[i].form_item
									})
								}
							}
						}
					}).catch((err) => {
						console.log(err)
					})
				console.log("noticeList等于", this.noticeList)
				this.getOrderList(0)
				this.getOrderList(1)
				this.show = false
			},
			goJoinClass() {
				uni.navigateTo({
					url: '../../../packageB/pages/joinClass/joinClass',
					success() {
						console.log("跳转成功")
					},
					fail() {
						console.log("跳转失败")
					},
					complete() {
						console.log("跳转完成")
					}
				})
			},
			goCreateClass() {
				uni.navigateTo({
					url: '../../../packageB/pages/createClass/createClass',
					success() {
						console.log("跳转成功")
					},
					fail() {
						console.log("跳转失败")
					},
					complete() {
						console.log("跳转完成")
					}
				})
			},
			reachBottom() {
				console.log("current:",this.current)
				if(this.current == 0){
					if (this.noticeList.length <= this.onePageData) {
						this.loadStatus.splice(this.current, 1, "nomore")
					} else if (this.currentNoticePage >= this.noticeList
						.length) {
						this.loadStatus.splice(this.current, 1, "nomore")
					}
					// 此tab为空数据
					else {
						this.loadStatus.splice(this.current, 1, "loading")
						setTimeout(() => {
							this.getOrderList(this.current);
						}, 1200);
					}
				}else{
					if (this.dataList.length <= this.onePageData) {
						this.loadStatus.splice(this.current, 1, "nomore")
					} else if (this.currentClassPage >= this.dataList.length) {
						this.loadStatus.splice(this.current, 1, "nomore")
					}
					// 此tab为空数据
					else {
						this.loadStatus.splice(this.current, 1, "loading")
						setTimeout(() => {
							this.getOrderList(this.current);
						}, 1200);
					}
				}
			},
			// 页面数据
			getOrderList(idx) {
				if (idx == 0) {
					for (let i = 0; i < this.onePageData; i++) {
						let index = this.currentNoticePage;
						if (this.currentNoticePage >= this.noticeList.length) break
						this.currentNoticePage++;
						let data = JSON.parse(JSON.stringify(this.noticeList[index]));
						console.log(data)
						console.log("currentNoticePage:",this.currentNoticePage)
						data.id = this.$u.guid();
						this.orderList[idx].push(data);
					}
					console.log("广播页已完成第" + this.count++ + "次")
					if (this.currentNoticePage >= this.noticeList.length) {
						this.loadStatus.splice(this.current, 1, "nomore")
					} else {
						this.loadStatus.splice(this.current, 1, "loadmore")
					}
				} else {
					for (let i = 0; i < this.onePageData; i++) {
						let index = this.currentClassPage;
						if (this.currentClassPage >= this.dataList.length) break
						this.currentClassPage++;
						let data = JSON.parse(JSON.stringify(this.dataList[index]));
						console.log(data)
						console.log("currentClassPage:",this.currentClassPage)
						data.id = this.$u.guid();
						this.orderList[idx].push(data);
					}
					console.log("班级页已完成第" + this.count++ + "次")
					if (this.currentClassPage >= this.dataList.length) {
						this.loadStatus.splice(this.current, 1, "nomore")
					} else {
						this.loadStatus.splice(this.current, 1, "loadmore")
					}
				}
			},
			// tab栏切换
			change(index) {
				this.swiperCurrent = index;
				// this.getOrderList(index);
			},
			transition({
				detail: {
					dx
				}
			}) {
				this.$refs.tabs.setDx(dx);
			},
			animationfinish({
				detail: {
					current
				}
			}) {
				this.$refs.tabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			}
		}
	};
</script>

<style>
	/* #ifndef H5 */
	page {
		height: 100%;
		background-color: #f7f8fa;
	}

	/* #endif */
</style>

<style lang="scss" scoped>
	.loading {
		position: fixed;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 150rpx;
		width: 150rpx;
		left: 50%;
		top: 50%;
		margin-top: -75rpx;
		margin-left: -75rpx;
		border: 1px solid rgba(255, 255, 255, 0.1);
		border-radius: 10upx;
		background: #040038;
		z-index: 999;
	}

	.class-operation {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 710rpx;
		background-color: #ffffff;
		margin: 20rpx auto;
		border-radius: 10rpx;
		box-shadow: 0 0 20upx 0 rgba(0, 0, 0, 0.10);
		box-sizing: border-box;
		padding: 10rpx;

		.card {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 20rpx;
			width: 50%;

			.operation {

				// margin-right: 30rpx;
				.name {
					margin-left: 0;
					font-size: 36rpx;
					padding: 10rpx 10rpx 0 0rpx;
					font-weight: bold;
				}

				;

				.desc {
					color: #9f9f9f;
					font-size: 24rpx;
					margin-top: 20rpx;
					padding: 0 10rpx 10rpx 0rpx;
				}
			}
		}
	}

	.wrap {
		display: flex;
		flex-direction: column;
		height: calc(100vh - var(--window-top) - 78px);
		width: 100%;
	}

	.swiper-box {
		flex: 1;
	}

	.swiper-item {
		height: 100%;
	}

	.select {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 380rpx;
		margin: 20rpx auto;
	}

	.sub {
		width: 300rpx;
	}

	.slot-wrap {
		display: flex;
		align-items: center;
		/* 如果您想让slot内容占满整个导航栏的宽度 */
		/* flex: 1; */
		/* 如果您想让slot内容与导航栏左右有空隙 */
		padding: 0 30rpx;
	}

	.name {
		margin-left: 15rpx;
		font-size: 18px;
		font-weight: bold;
		line-height: 1;
	}
</style>
